@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital@1&display=swap');
body {
    background-size: cover;
}

.lm-underline {
    position: relative;
}

.lm-underline:after {
    position: absolute;
    content: ' ';
    right: 5%;
    left: 5%;
    bottom: 0;
    height: 1px;
    background-image: -webkit-linear-gradient(left, transparent, #fff, transparent);
    background-image: linear-gradient(90deg, transparent, #fff, transparent);
}

.lm-tabel {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: 'Open Sans';
    position: relative;
    /* top: 55%;
    left: 50%;
    transform: translate(-50%, -50%); */
}

.lm-item {
    overflow: hidden;
    -webkit-transform: scale(0.85);
    transform: scale(0.85);
    vertical-align: center;
    width: 25%;
    box-shadow: 0px 3px 55px rgba(255, 0, 230, 0.15);
    display: inline-block;
    position: relative;
    -webkit-transition: -webkit-transform 0.3s ease-out, -webkit-filter 0.3s;
    transition: -webkit-transform 0.3s ease-out, -webkit-filter 0.3s;
    transition: transform 0.3s ease-out, filter 0.3s;
    transition: transform 0.3s ease-out, filter 0.3s, -webkit-transform 0.3s ease-out, -webkit-filter 0.3s;
}

.lm-item:hover {
    -webkit-transform: scale(.9);
    transform: scale(.9);
}

.lm-item-1 .lm-item-bottom,
.lm-item-1 .lm-item-top {
    /* background-color: #341b37; */
    /* background-image: -webkit-linear-gradient(135deg, #85468c, #241326);
    background-image: linear-gradient(-45deg, #85468c, #241326); */
    background-image: linear-gradient(135deg, var(--violet), var(--purple));
}

.lm-item-2 .lm-item-bottom,
.lm-item-2 .lm-item-top {
    background-image: linear-gradient(135deg, var(--violet), var(--purple));
}

.lm-item-3 .lm-item-bottom,
.lm-item-3 .lm-item-top {
    background-image: linear-gradient(135deg, var(--violet), var(--purple));
}

.lm-item-4 .lm-item-bottom,
.lm-item-4 .lm-item-top {
    background-image: linear-gradient(135deg, var(--violet), var(--purple));
}

.icon-select:hover {
    cursor: pointer;
}

.lm-item:not(:hover).blur {
    -webkit-filter: blur(3px);
    filter: blur(3px);
}

.lm-item:after,
.lm-item:before {
    z-index: -1;
    content: ' ';
    position: absolute;
    left: 10px;
    top: 80%;
    bottom: 18px;
    right: 10px;
    -webkit-transform-origin: bottom center;
    transform-origin: bottom center;
    box-shadow: 0px 25px 10px -8px rgba(0, 0, 0, 0.4);
}

.lm-item:after {
    -webkit-transform: rotate(6deg) translate3d(15px, 0, 0);
    transform: rotate(6deg) translate3d(15px, 0, 0);
}

.lm-item:before {
    -webkit-transform: rotate(-6deg) translate3d(-15px, 0, 0);
    transform: rotate(-6deg) translate3d(-15px, 0, 0);
}

.lm-item-top {
    padding-top: 20px;
    text-transform: uppercase;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    text-align: center;
}

.lm-item-title {
    color: #e3dfe2;
    font: 100 30px/70px 'Open Sans';
}

.lm-item-price {
    background: rgba(255, 255, 255, 0.05);
    margin-top: 20px;
    font-size: 40px;
    line-height: 38px;
    color: #fff;
}

.lm-item-4 .lm-item-price {
    line-height: 35px;
    margin-top: 0px;
}

.lm-item-price i {
    font-size: 15px;
}

.lm-item-body {
    padding: 15px;
    background: #fff;
}

.lm-item-4 .lm-item-body {
    padding: 10px;
}

.lm-item-desc {
    text-align: center;
    margin-bottom: 15px;
    text-transform: uppercase;
}

.lm-item-1,
.lm-item-2,
.lm-item-3 {
    height: auto;
}

.lm-item-desc strong {
    color: #868686;
    font: 400 16px 'Open Sans';
}

.lm-item-desc p {
    color: #b4b4b4;
    margin: 0;
}

.lm-item-list {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none;
    color: black;
    text-align: left;
}

.lm-item-list li {
    color: #121212;
    border-bottom: 1px dotted rgba(0, 0, 0, 0.2);
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}


/* .lm-item-list li:nth-child(2n+1) {
    background-color: #fbfbfb;
} */

.lm-item-list .icon-cancel,
.lm-item-list .icon-ok {
    float: right;
}

.icon-select {
    float: right;
}

.lm-item-list .icon-ok {
    color: var(--yellow);
}

.lm-item-list .icon-cancel {
    color: #00f5ff;
}

.lm-item-link {
    text-align: center;
    margin-top: 15px;
}

.lm-item-link a {
    text-decoration: none;
}

.lm-item-link display inline-block {
    height: 54px;
}

.lm-item-link display inline-block:hover {
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    -webkit-transform: translate3d(0, -3px, 0);
    transform: translate3d(0, -3px, 0);
}

.lm-item-link display inline-block:active {
    -webkit-transition: -webkit-transform 0.1s;
    transition: -webkit-transform 0.1s;
    transition: transform 0.1s;
    transition: transform 0.1s, -webkit-transform 0.1s;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.lm-item-link text {
    font-size: 21px;
    font-weight: 700;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.lm-item-bottom {
    text-align: center;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
    padding: 1px;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    min-height: 20px;
}

.lm-item-bottom span {
    color: #fff;
    font-weight: 400;
    margin-right: 3px;
    -webkit-transform: skewX(-10deg);
    transform: skewX(-10deg);
    display: inline-block;
}

@font-face {
    font-family: 'fontello';
    src: url("data:application/font-woff;base64,") format('woff');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: 0.2em;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    margin-left: 0.2em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.lm-real-price {
    text-decoration: line-through;
    font-size: 60%;
}

.check-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}


/* .icon-dollar:before {
    content: '\e806';
} */

.icon-cancel:before {
    content: '\e807';
}

.icon-ok:before {
    content: '\e808';
}

.icon-select::before {
    content: '\e807';
}

#total {
    background: none;
    border: none;
}

#fake_button {
    background: none;
    border: none;
    z-index: 100;
}

@media screen and (max-width: 960px) {
    .lm-tabel {
        flex-direction: column;
        top: 10%;
        left: 0%;
        transform: translate(0, 0);
    }
    .lm-item {
        width: 100%;
    }
    .lm-item-price {
        padding: 5px;
    }
}

.bn3637 {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0.7rem 2rem;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    text-decoration: none;
    color: #00f5ff;
    backface-visibility: hidden;
    border: 0.1rem solid transparent;
    border-radius: 3rem;
    background-color: white;
}

.bn36 {
    border-color: #00f5ff;
    transition: transform 0.2s cubic-bezier(0.235, 0, 0.05, 0.95);
}

.bn36:hover {
    transform: perspective(1px) scale3d(1.044, 1.044, 1) translateZ(0) !important;
}

.perLecCost {
    color: white;
    font-family: 'Montserrat', sans-serif;
}

.featured-ribbon {
    width: 200px;
    background: var(--blue);
    position: absolute;
    top: 15px;
    left: -60px;
    transform: rotate(-45deg);
    text-align: center;
    line-height: 35px;
    letter-spacing: 0.01em;
    font-size: 1.1em;
    font-weight: 700;
    color: white;
    text-transform: uppercase;
    -webkit-transform: rotate(-45deg);
}

.only {
    font-size: 1.9rem;
}

.tooltip {
    position: absolute;
    /* width: 100%; */
    top: 3px;
    right: 3px;
    color: rgba(242, 242, 242, 0.5);
    display: inline-block;
}


/* Tooltip text */

.tooltiptext {
    top: 30px;
    width: 90%;
    align-self: center;
    margin-left: 15px;
    visibility: hidden;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    opacity: 0;
    transition: opacity 1s;
    position: absolute;
    z-index: 10;
}


/* Show the tooltip text when you mouse over the tooltip container */

.tooltip:hover+.tooltiptext {
    visibility: visible;
    opacity: 1;
}