.modal {
  font-size: 12px;
  background: linear-gradient(var(--violet), var(--purple));
  color: var(--white);
  margin: 0px;
  padding: 1rem;
}

.modal-container {
  display: flex;
  align-items: baseline;
  justify-content: center;
}

.modal-1,
.modal-2 {
  padding: 0 10px;
  margin: 0 .5rem;
  width: 100%;
  text-align: left;
}

.modal-1 img,
.modal-2 img {
  width: 100%;
}

.modal-1 .btn,
.modal-2 .btn {
  margin: auto 5rem;
  text-align: center;
  width: 50%;
  border-color: transparent;
  color: white;
  background: radial-gradient(var(--purple), var(--blue));
}

.modal-1 ul {
  list-style: disc;
  margin-left: 1rem;
  margin-bottom: 1rem;
}

.modal .modal-1>.header,
.modal .modal-2>.header {
  width: 100%;
  border-bottom: 2px solid white;
  font-size: 1.5rem;
  text-align: center;
  padding: 5px;
  color: var(--white);
  margin-bottom: .5rem;
}

.modal .modal-1>.content,
.modal .modal-2>.content {
  width: 100%;
  font-size: 12px;
  padding: 1rem 0;
}

.modal>.actions {
  width: 100%;
  padding: 1rem;
  margin: auto;
  text-align: center;
}

.modal>.close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: var(--violet);
  border-radius: 2rem;
  border: 1px solid var(--white);
  box-shadow: 0px 3px 55px rgba(255, 255, 255, .5);
  color: var(--white);
}

.popup-content {
  background: var(--violet);
  border: none;
